.container {
  max-width: 1020px;
  padding: 0 20px;
  margin: 0 auto;

  @include onDesktop {
    padding: 0 40px;
  }
}

.page {
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: #3e3e3e;
  scroll-behavior: smooth;
  transition: all 0.3s;

  &__content--with-menu {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0%);
    }
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }
  }

  &__section-title {
    margin: 0 0 32px;
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -1.5px;
    color: $blackColor;

    @include onDesktop {
      margin-bottom: 56px;
      font-size: 48px;
      line-height: 48px;
    }

    &::after {
      content: "";
      display: block;
      height: 23px;
      width: 16px;
      margin: 4px auto 0;
      background-image: url(../images/crown.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;

    }
  }
}
